.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Ensure it appears above other elements */
}
.maincontainer.left {
  margin-right: -10px; /* Adjust this value to control the overlap */
}

.maincontainer.right {
  margin-left: -10px; /* Adjust this value to control the overlap */
}
.maincontainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 25px;
  padding-right: 0;
 padding-left: 0;
 margin-top: 15px;
 margin-bottom: 15px;
 
}
.bothimg{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  margin-left: -20px;
  
}
.profilepicimg {
  border-radius: 9999px;
  width: 160px;
  height: 160px;
  aspect-ratio: 1 / 1;
  object-fit: cover;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 4px 4px 0px #00000040;
 


}
