.modalContent {
  position: relative;
  width: 100vw;
  height: 100vh;
  background: white; /* White background */
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  box-sizing: border-box;
}

.closeButton {
  position: absolute;
