.entercode-container
{
display: flex;




justify-content:space-between;
position: relative;

}
.entercode-bg
{

    width: 100%;

       
    height: auto;
    background-size: cover;
    background-repeat: no-repeat;
  
   
}
.responsive-bg
{
    height: 100%;
    width: 100%;
}




.entercode-main
{
    width:100%
}
.entercode-box
{
    background: #FFFFFF;

    display: flex;
    flex-direction: column;
/* justify-content: space-evenly; */
gap:24px;
align-items: center;
height: 100dvh;
width:85%;

}
.logo-progressbar2
{
    margin-top: 50px;
    gap: 16px;
    display: flex;
    flex-direction: column;
}
.logo-arrow2
{
    display: flex;

    align-items: center;
    justify-content: flex-start;
    padding: 0px;
    margin: 0px;
   
    height: 57px;
    position: relative;


}
.logo{

    position: absolute;
    left: 50%;
      transform: translate(-50%);
    
    }


    

.entercode-text
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap:24px;
    
}
.entercode-details
{
    display: flex;
    flex-direction: column;
   height: 100dvh;

}
.entercode-content
{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding-bottom: 36px;
}
