.entercode-input
  {
    border: 1px solid #6C6C6C;
    color: #6C6C6C;
    height:52px;
    width: 50px;
    border-radius: 8px;
    background: #FFFFFF;
    text-align: center;

}


.entercode-input::placeholder
 {
    font-family: Poppins;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: 0.04em;
    color:#6C6C6C;

}
.custom-input:focus {
    outline: none !important;
    box-shadow: none !important;
    border-color:#6C6C6C !important;
  }
.entercode-input.error {
    border-color: red;
  }
  .error-message
  {
    font-family: Poppins;
font-size: 14px;
font-weight: 600;
line-height: 22px;
text-align: left;
color: #FF4747 !important;

  }
  


.entercode-input:active
{


}
.resend-timer
{
    display: flex;
    justify-content: space-between;
    margin-top:24px ;
}
.resend-timer a
{
    font-family: Poppins, sans-serif;
    font-size: 16px;
    font-weight: 600;
    line-height: 18.77px;
    letter-spacing: 0.04em;
    text-align: left;
    color: 
    #6C6C6C;
    
}
.resend-timer span
{
    font-family: Poppins, sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: 18.77px;
    letter-spacing: 0.04em;
    text-align: right;
    color: 
    #6C6C6C;
    
}
.entercode-btn
{
    height:48px;
    width:100%;
    max-width:250px;
    /* margin-top:110px ; */
    background: linear-gradient(180deg, #FC8C66 -4.17%, #F76A7B 110.42%);

    box-shadow: 0px 4px 10px 0px #00000029;
    color:#FFFFFF;
    font-family: Poppins;
font-size: 16px;
font-weight: 600;
line-height: 24px;
letter-spacing: 0.06em;
text-align: center;

align-items: center;
border: none;
border-radius: 42px;
}

@media only screen and (max-width:750px){
.entercode-bg
{
    display:none;
}
.entercode-box
{
width: 100%;
}
}